import React from 'react';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'
import { Checkbox, FormGroup, makeStyles } from '@material-ui/core';

const useStyles = makeStyles({
    root: {
        '& input': {
            '&:hover+label': {
                background: '#f2c452',
                color: '#fff',
                borderColor: '#ffd469'
            }
        }
    },
    disabled: {
        '&:after': {
            content: '""',
            position: 'absolute',
            width: '100%',
            height: '2px',
            borderBottom: '1px solid #b7b7b7',
            left: '50%',
            top: '50%',
            transform: 'translate(-50%,-50%)'
        }
    }
});

// sizes {label: String, disabled: boolean}

const SizeProduct = (props) => {
    const classes = useStyles();
    return (
        <FormGroup row >
            {props.sizes.map(v =>
                <Checkbox
                    disabled={v.disabled}
                    className={classes.root}
                    name={v.label}
                    icon={<label className={v.disabled ? classes.disabled : ''} css={css`
                    padding: 5px 12px;
    background: #fff;
    border: 1px solid;
    border-color: ${v.disabled ? '#d2cab7' : '#f2c452'};
    font-size: 12px;
    color: ${v.disabled ? '#b7b7b7' : '#000'};
    text-transform: uppercase;
    line-height: 18px;
    pointer-events: ${v.disabled ? 'none' : 'initial'};
                        `}>
                        {v.label}</label>}
                    checkedIcon={<label css={css`
                        border: 1px solid #ffd469;
                        font-size: 12px;
                        padding: 5px 12px;
                        line-height: 18px;
                        background: #f2c452;
                        color: #fff;
                        text-transform: uppercase;
                        `}>
                        {v.label}</label>}
                    disableRipple
                    disableFocusRipple
                    disableTouchRipple


                />)}
        </FormGroup>
    );
};

export default SizeProduct;
