import React, { useState } from 'react';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react'
import { Button, ButtonGroup, FormGroup } from '@material-ui/core';

const Quantity = (props) => {
    const [counter, setCounter] = useState(1);

    return (
        <ButtonGroup size="large" css={css`align-items: center; border: 1px solid #ddb450;display:inline-block;`}>
            <Button disabled={counter === 1} disableRipple disableFocusRipple disableTouchRipple variant="text" onClick={() => setCounter(counter => counter - 1)}
                css={css`color: #aa8531`}>
                -
            </Button>
            <span css={css`text-align: center;color: #900c19;font-size: 18px;`}>{counter}</span>
            <Button disableRipple disableFocusRipple disableTouchRipple variant="text" onClick={() => setCounter(counter => counter + 1)}
                css={css`color: #aa8531`}>
                +
            </Button>
        </ButtonGroup>
    );
};

export default Quantity;
